    monospace;
}

* {
  font-family: "Montserrat", sans-serif;
}
:root {
  background-color: #283747;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

.portfolioContainer {
  display: flex;
  flex-direction: row;
  grid-gap: 20px;
  gap: 20px;
  align-items: center;
  background-color: #3c1874;
  padding: 3rem 0;
  -webkit-filter: drop-shadow(0 3px 9px rgba(0, 0, 0, 0.404));
          filter: drop-shadow(0 3px 9px rgba(0, 0, 0, 0.404));
  flex: 1 1 33vw;
}

@media screen and (max-width: 810px) {
  .portfolioContainer {
    flex-direction: column;
    justify-content: center;
    margin: 0 1rem;
  }
}

.displayCardImg {
  width: 100%;
  object-fit: cover;
  border-radius: 12px;
  transition: 0.3s;
}

.displayCardImg:hover {
  opacity: 0.45;
  mix-blend-mode: screen;
}

.displayCardImgLink {
  background-color: #3c1874;
  display: inline-block;
  border-radius: 12px;
  transition: 00.3s;
  text-decoration: none;
  text-align: center;
  font-size: 0;
}

div.clickForMoreContainer {
  display: flex;
  justify-content: center;
}

p.clickForMore {
  color: #f3f3f3;
  font-size: small;
  padding-top: 0;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

p.clickForMore:hover {
  transform: scale(1.01);
}

p.moreInfo {
  background-color: rgba(40, 55, 71, 0.5);
  padding: 1rem 2rem;
  border-radius: 7px;
  margin-top: 3rem;
  color: #f3f3f3;
  -webkit-filter: drop-shadow(1px 1px 1.5px black);
          filter: drop-shadow(1px 1px 1.5px black);
  font-size: 1.5rem;
}

p.moreInfo.close {
  display: none;
}

p.displayCardHeading {
  color: #f3f3f3;
  padding-top: 1.5rem;
  font-size: 1.5rem;
  margin-bottom: 0.2rem;
}

* {
  margin: 0px;
  padding: 0px;
}
.aboutMe {
  display: grid;
  margin: 4rem 3rem;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: 1fr 5fr;
  grid-template-areas:
    "image heading"
    "image paragraph";
}

.image img {
  /* margin: 0rem 0rem 0rem 0rem; */
  width: 100%;
  object-fit: cover;
  border-radius: 50%;
}

h2,
p {
  text-align: center;
  margin-bottom: 1rem;
}

.image {
  grid-area: image;
}

.heading {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  grid-area: heading;
  color: #f3f3f3;
}

.paragraph {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  color: #f3f3f3;
  grid-area: paragraph;
  font-size: 1.4rem;
  padding: 0 5rem;
  line-height: 2.5rem;
  word-spacing: 0.3rem;
}

@media screen and (max-width: 1115px) {
  .aboutMe {
    margin: 2rem 0rem 0rem;
    grid-column-gap: 0rem;
    -webkit-column-gap: 0rem;
            column-gap: 0rem;
    grid-template-columns: 1fr;
    grid-template-rows: 0.3fr 0.1fr 1fr;
    grid-template-areas:
      "image"
      "heading"
      "paragraph";
  }
  .heading {
    align-items: flex-start;
  }
  .paragraph {
    box-sizing: border-box;
    align-items: flex-start;
    padding: 0 2rem;
    font-size: 1.4rem;
  }
  .image {
    display: flex;
    width: 100%;
    justify-content: center;
  }
  .image img {
    width: 300px;
    margin-bottom: 1rem;
    max-height: 300px;
  }
}

.carousel {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 8rem;
}

.carouselImage {
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: -1;
}

.leftArrow {
  position: absolute;
  top: 50%;
  left: 32px;
  font-size: 3rem;
  color: black;
  z-index: 1;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #de354c;
}

.rightArrow {
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3rem;
  color: black;
  z-index: 1;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #de354c;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slideActive {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}

@media screen and (max-width: 486px) {
  .carouselImage {
    width: 300px;
    height: 200px;
  }
}

@media screen and (min-width: 487px) {
  .carouselImage {
    width: 425px;
    height: 250px;
  }
}

@media screen and (min-width: 650px) {
  .carouselImage {
    width: 600px;
    height: 300px;
  }
}

@media screen and (min-width: 1366px) {
  .carouselImage {
    width: 1200px;
    height: 600px;
  }
}

